:root {
  font-size: 125%;
  --margins: 20px;
  --main-width: 960px;
  --border-radius: 4px;
}

body {
  margin: 0;
  font-family: Arial, Verdana, sans-serif, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #333333;
  color: #cccccc;
  text-rendering: optimizeLegibility;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
  color: #5990ff;
  text-decoration: none;
  outline:none;
}

a:hover span,
a:focus span,
a:active span {
  text-decoration: underline;
}

img[src$=".gif"], img[src$=".png"] {
    image-rendering: -webkit-optimize-contrast;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
